.w-screen {
  width: 100vw;
}
.max-w-screen {
  max-width: 100vw;
}
.min-w-screen {
  min-width: 100vw;
}
.h-screen {
  height: 100vh;
}
.max-h-screen {
  max-height: 100vh;
}
.min-h-screen {
  min-height: 100vh;
}
@each $item in (0, 0), (auto, auto), (full, 100%) {
  $name: nth($item, 1);
  $value: nth($item, 2);

  .w-#{$name} {
    width: $value;
  }
  .max-w-#{$name} {
    max-width: $value;
  }
  .min-w-#{$name} {
    min-width: $value;
  }
  .h-#{$name} {
    height: $value;
  }
  .max-h-#{$name} {
    max-height: $value;
  }
  .min-h-#{$name} {
    min-height: $value;
  }
}
@for $denominator from 2 through 12 {
  @for $i from 1 through length($widths) {
    $value: nth($widths, $i);
    .w-#{$i} {
      width: $value;
    }
    .max-w-#{$i} {
      max-width: $value;
    }
    .min-w-#{$i} {
      min-width: $value;
    }
  }

  @for $numerator from 1 through ($denominator - 1) {
    .w-#{$numerator}\/#{$denominator} {
      width: $numerator / $denominator * 100%;
    }
    .max-w-#{$numerator}\/#{$denominator} {
      max-width: $numerator / $denominator * 100%;
    }
    .min-w-#{$numerator}\/#{$denominator} {
      min-width: $numerator / $denominator * 100%;
    }
    .h-#{$numerator}\/#{$denominator} {
      height: $numerator / $denominator * 100%;
    }
    .max-h-#{$numerator}\/#{$denominator} {
      max-height: $numerator / $denominator * 100%;
    }
    .min-h-#{$numerator}\/#{$denominator} {
      min-height: $numerator / $denominator * 100%;
    }
  }
}
